import { RegexTestResponse, State } from "../../regex";
import { PayloadAction } from "@reduxjs/toolkit";

export const getTestResultRequest = (state: State) => {
    state.isTesting = true;
}

export const getTestResultSuccess = (state: State, action: PayloadAction<RegexTestResponse>) => {
    state.isTesting = false;
    state.result = action.payload;
}

export const getTestResultFailure  = (state: State, action: PayloadAction<number | unknown>) => {
    state.isTesting = false;
    state.result = null;
}

export const regexChanging = (state: State, action: PayloadAction<string>) => {
    state.regex = action.payload;
}

export const regexChanged = () => {}

export const inputChanging = (state: State, action: PayloadAction<string>) => {
    state.input = action.payload;
}

export const inputChanged = () => {}
