import { CidrResponse, State } from "../../cidr";
import { PayloadAction, Action } from "@reduxjs/toolkit";
import { IPv4Address } from "../../IPv4Address";


export const getCidrRequest = (state: State) => {
    state.isGetting = true;
}

export const getCidrSuccess = (state: State, action: PayloadAction<CidrResponse>) => {

    state.cidr = action.payload.ipAddress;
    state.isGetting = false;
}
export const getCidrFailure = (state: State, action: PayloadAction<number | unknown>) => {
    state.cidr = null;
    state.isGetting = false;
}

export const ipChanging = (state: State, action: PayloadAction<IPv4Address>) => {
    state.ipAddress = action.payload;
}

export const maskChanging = (state: State, action: PayloadAction<IPv4Address>) => {
    state.netMask = action.payload;
}
