import { createSlice } from "@reduxjs/toolkit";
import * as reducers from "./reducers";
import { State } from "../../regex";

export const initialState: State = {
    input: null,
    isTesting: false,
    regex: null,
    result: null,
};

const regexSlice = createSlice({
    name: "regex",
    initialState: initialState,
    reducers: {
        ...reducers,
    },
});

export const { getTestResultRequest, getTestResultSuccess, getTestResultFailure, regexChanging, inputChanging } = regexSlice.actions;

export default regexSlice.reducer;