import { createSlice } from "@reduxjs/toolkit";
import * as reducers from "./reducers";
import { State } from "../../cidr";

export const initialState: State = {
    cidr: null,
    ipAddress: null,
    isGetting: false,
    netMask: null,
};

const regexSlice = createSlice({
    name: "cidr",
    initialState: initialState,
    reducers: {
        ...reducers,
    },
});

export const { getCidrRequest, getCidrSuccess, getCidrFailure, ipChanging, maskChanging } = regexSlice.actions;

export default regexSlice.reducer;