import debounce from "lodash.debounce";
import * as React from "react";
import { connect } from "react-redux";

import { DispatchProps } from "../global";
import { IPv4Address } from "../IPv4Address";

import { State } from "../cidr";
import * as actions from "../Redux/Cidr/slice";

import IPAddress from "../Components/IPAddress";
import TextBox from "../Components/TextBox";
import { useDispatch } from "react-redux";
import { useEffect } from "react";


//class Cidr extends React.Component<CidrProps, any> {

const Cidr: React.FC<CidrProps> = (props) => {

    const dispatch = useDispatch();

    const ipChanged = (e: IPv4Address) => {
        stateChangedDB(e, props.netMask);
        props.ipChanged(e);
        dispatch(actions.ipChanging(e));
    }

    const maskChanged = (e: IPv4Address) => {
        stateChangedDB(props.ipAddress, e);
        props.maskChanged(e);
        dispatch(actions.maskChanging(e));
    }

    const stateChangedDB = (ip: IPv4Address, mask: IPv4Address) => {
        debounce(props.stateChanged, 250);
    }

    useEffect(() => {
            if (props.ipAddress === null || props.netMask === null) return;
            const cidr = (props.ipAddress).toCidr(props.netMask).toString();
            dispatch(actions.getCidrSuccess(cidr));
    }, [props.ipAddress, props.netMask]);
    

    let result = null;

    if (props.cidr) {
        result = <TextBox id="result" className="clickable" value={props.cidr} readOnly={true} label="CIDR Notation" onClick={(e: React.MouseEvent<HTMLInputElement>) => navigator.clipboard.writeText(e.currentTarget.value)} />;
    }

    return (
        <>
            <section className="row">
                <div className="col">
                    <fieldset>
                        <IPAddress id="address" label="IP Address" value={props.ipAddress} onChange={ipChanged} />
                        <IPAddress id="mask" label="Subnet Mask" value={props.netMask} onChange={maskChanged} />
                    </fieldset>
                </div>
            </section>
            <section className="row">
                <div className="col cidr-result ">
                    {result}
                </div>
            </section>
        </>
    );

}

export default Cidr;

interface CidrProps extends DispatchProps {
    cidr?: string;
    ipAddress?: IPv4Address;
    netMask?: IPv4Address;
    ipChanged: (e) => void;
    maskChanged: (e) => void;
    stateChanged: (ip: IPv4Address, mask: IPv4Address) => void;
}
